<!--司机签到签退查询-->
<template>
    <div class="tw-template-wrapper">
        <el-form :inline="true" :model="query" size="small" class="tw-query-bar">
            <el-form-item>
                <el-autocomplete class="inline-input" v-model="query.vehicle"
                                 :fetch-suggestions="queryVehicleSearch" :trigger-on-focus="false" placeholder="请输入车牌号码"></el-autocomplete>
            </el-form-item>
            <el-form-item>
                <el-input v-model="query.icVehicle" placeholder="IC卡车牌号"></el-input>
            </el-form-item>
            <el-form-item>
                <el-input v-model="query.certificate" placeholder="服务资格证号"></el-input>
            </el-form-item>
            <el-form-item>
                <el-select v-model="query.area" placeholder="区域">
                    <el-option v-for="item in getRegionReal" :key="item.value" :label="item.label" :value="item.value"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-date-picker arrow-control type="date" v-model="query.sTime" placeholder="签到开始时间"></el-date-picker>
            </el-form-item>
            <el-form-item>
                <el-date-picker arrow-control type="date" v-model="query.eTime" placeholder="签到结束时间"></el-date-picker>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="handleQueryClick">查询</el-button>
                <el-button type="primary" @click="handleExportClick">导出</el-button>
            </el-form-item>
        </el-form>
        <div class="tw-query-panel">
            <el-table :data="filterTableList" v-loading="table.loading" border size="small" height="calc(100% - 42px)" style="width: 100%; margin-bottom: 10px;">
                <el-table-column type="index" label="序号" width="60"></el-table-column>
                <el-table-column prop="OPERATION_VEHICLENO" label="当前车牌号" min-width="120"></el-table-column>
                <el-table-column prop="IC_REGISTER_VEHICLENO" label="IC卡车牌号码" width="300"></el-table-column>
                <el-table-column prop="AREA_NAME" label="区域" min-width="120"></el-table-column>
                <el-table-column prop="CERTIFICATE" label="服务资格证号" width="250"></el-table-column>
                <el-table-column prop="SIGN_TIME" label="签到时间" width="250"></el-table-column>
                <el-table-column prop="SIGN_OFF_TIME" label="签退时间" width="250"></el-table-column>
            </el-table>
            <el-pagination background :page-size="table.pageSize" :current-page="table.currentPage" :total="table.total" layout="prev, pager, next, total" @current-change="handleTablePageCurrentChange"></el-pagination>
        </div>
    </div>
</template>

<script>
    import _ from 'underscore'
    import axios from 'axios'
    import moment from 'moment'
    import TWUnit from '../../components/TWTableColumn/TWUnit'
    import {formatCustomizeDate, formatDateTime} from '../../assets/js/util'
    import {mapGetters} from 'vuex'

    export default {
        name: "BicycleOperation",
        data() {
            return {
                query: {
                    vehicle: '',
                    area:'',
                    icVehicle:'',
                    certificate:'',
                    sTime: '',
                    eTime: '',
                },
                table: {
                    loading: false,
                    data: [],
                    pageSize: 20,
                    currentPage: 1,
                    total: 0
                }
            }
        },
        mounted() {
            this.$nextTick(() => {
                this.query.sTime = formatCustomizeDate(moment(), 'YYYY-MM-DD');
                this.query.eTime = formatCustomizeDate(moment(), 'YYYY-MM-DD');
                this.getDriverSignAndSignOffQuery();
            });
        },
        computed: {
            ...mapGetters(['getLPNumber','getRegionReal']),
            filterTableList() {
                const {data, pageSize, currentPage} = this.table;
                const pageIndex = currentPage -1;
                return _.filter(data, (item, index) => {
                    return  index >= pageIndex * pageSize && index < currentPage * pageSize;
                })
            }
        },
        methods: {
            /*接口*/
            getDriverSignAndSignOffQuery(){
                this.table.loading = true;
                const {vehicle, area, icVehicle, certificate, sTime, eTime} = this.query;
                axios.get('industryOperation/getDriverSignAndSignOffQuery', {
                    baseURL: this.baseURL,
                    params: {
                        vehicle,
                        area,
                        icVehicle,
                        certificate,
                        sTime: sTime && moment(sTime).format('YYYY-MM-DD 00:00:00'),
                        eTime: eTime && moment(eTime).format('YYYY-MM-DD 23:59:59')
                    }
                }).then(res => {
                    this.table.data =  res.data;
                    this.table.total = res.data.length;
                    this.table.currentPage = 1;
                    this.table.loading = false;
                })
            },
            queryVehicleSearch(query, cb) {
                if(query.length < 3) cb = null;
                else cb(_.filter(this.getLPNumber, item => item.label.indexOf(query) > -1));
            },
            /*事件*/
            handleQueryClick() {
                this.table.currentPage = 1;
                this.getDriverSignAndSignOffQuery();
            },
            handleTablePageCurrentChange(index) {
                this.table.currentPage = index;
            },
            handleExportClick() {
                const {vehicle, area, icVehicle, certificate, sTime, eTime} = this.query;
                this.$confirm('是否需要导出?', '提示', {
                    confirmButtonText: '是',
                    cancelButtonText: '否',
                    cancelButtonClass: 'el-button--danger',
                    closeOnClickModal: false,
                    type: 'info',
                    center: true
                }).then(() => {
                    window.open(`${this.baseURL}industryOperation/getDriverSignAndSignOffQueryExcel?vehicle=${vehicle}&area=${area}&icVehicle=${icVehicle}&certificate=${certificate}&sTime=${sTime && moment(sTime).format('YYYY-MM-DD 00:00:00')}&eTime=${eTime && moment(eTime).format('YYYY-MM-DD 23:59:59')}`);
                }).catch(() => {});
            },
        },
        components: {
            'tw-unit':TWUnit
        }
    }
</script>

